@font-face {
  font-family: "Basier Circle";
  font-display: swap;
  font-weight: 400;
  src: url("https://assets.revolut.com/media/fonts/BasierCircle-Regular.woff2");
}

@font-face {
  font-family: "Basier Circle";
  font-weight: 500;
  font-display: swap;
  src: url("https://assets.revolut.com/media/fonts/BasierCircle-Medium.woff2");
}

@font-face {
  font-family: "Basier Circle";
  font-weight: 600;
  font-display: swap;
  src: url("https://assets.revolut.com/media/fonts/BasierCircle-Semibold.woff2");
}

@font-face {
  font-family: "Roboto Mono";
  font-weight: 400;
  font-display: swap;
  src: url("https://fonts.googleapis.com/css2?family=Roboto+Mono");
}

html,
body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  padding: 0;
  margin: 0;
  font-family: "Basier Circle", Roboto, -apple-system, "Helvetica Neue",
    Helvetica, "Segoe UI", Arial, sans-serif;
}

* {
  box-sizing: border-box;
  line-height: 1.65;
  outline: none;
}

/* ====================
*** REDOC OVERRIDES ***
=======================*/
/* Code snippets displayed on the right */
.redoc-json > code {
  font-size: 14px;
  font-family: Roboto, -apple-system, "Helvetica Neue", Helvetica, "Segoe UI",
    Arial, sans-serif;
  line-height: 40px;
}

/* Hide Redoc message: "Documentation Powered by ReDoc" */
/** internally, display is 'block' already **/
a[href^="https://github.com/Redocly/redoc"]
{
  width: 0;
  height: 0;
  overflow: hidden;
}

/* Keep search bar in view */
.menu-content {
  top: 100px !important;
  padding-top: 50px;
}

.noscroll {
  overflow: hidden;
}

/* Space major internal redoc sections */
[id^="tag/"],
[id^="operation/"],
[id^="section/"] {
  padding-top: 85px !important;
  padding-bottom: 0 !important;
}

button {
  padding: 0;
  border: 0;
  cursor: pointer;
}
